.jumbotron {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px;
}

.jumbotron .form{
  padding: 0px;
}

.jumbotron .form input {
padding: 8px;
margin-right: 3px;
width: 350px;
border-color: #212121;
}

.jumbotron .form .btn-submit{
  background-color: #212121;
  color: white;
  border:none ;
  padding: 10px;
}
.list {
  display: flex;
  justify-content: center;
  align-items: center;
 
}